<template>
  <div class="board">
    <note v-for="note in notes" :note="note" :onSelect="selectNote" :selectedNotes="selectedNotes"></note>
    <button v-on:click="addNote">Add a Note</button>
  </div>
</template>

<script>
  import Note from './Note';

  export default {
    components: { Note },
    data() {
      return {
        notes: [
          {
            content: 'I am a note',
            selected: false,
          },
          {
            content: 'I am a second note',
            selected: false,
          },
        ],
        selectedNotes: [],
      };
    },
    methods: {
      addNote() {
        this.notes.push({ content: 'I am another note', selected: false });
      },
      selectNote(note, element) {
        const theNote = note;
        const selected = !theNote.selected;

        theNote.selected = !theNote.selected;
        if (selected) {
          this.selectedNotes.push(element);
        } else {
          this.selectedNotes = this.selectedNotes.filter(noteElement => noteElement !== element);
        }
      },
    },
  };
</script>

<style rel="stylesheet/scss" lang="scss">
  .board {
    background: #eee;
    height: 500px;
    position: relative;
  }
</style>



