<template>
  <div :class="{ note: true, selected: note.selected }" @click.shift="onSelect(note, element)">
    <div class="handle"></div>
    {{ note.content }}
  </div>
</template>

<script>
  import interact from 'interactjs';

  export default {
    props: ['note', 'onSelect', 'selectedNotes'],
    computed: {
      element() {
        return this.$el;
      },
    },
    mounted() {
      interact(this.$el)
        .draggable({
          // enable inertial throwing
          inertia: true,
          // keep the element within the area of it's parent
          restrict: {
            restriction: 'parent',
            endOnly: true,
            elementRect: { top: 0, left: 0, bottom: 1, right: 1 },
          },
          // enable autoScroll
          autoScroll: true,
          onmove: this.dragMoveListener,
        })
        .allowFrom(this.$el.querySelector('.handle'));
    },
    methods: {
      dragMoveListener(event) {
        if (this.selectedNotes.length) {
          this.selectedNotes.forEach((selectedNote) => {
            if (selectedNote !== this.$el) {
              this.moveNote(selectedNote, event.dx, event.dy);
            }
          });
        }

        this.moveNote(event.target, event.dx, event.dy);
      },
      moveNote(target, dx, dy) {
        const note = target;
        const x = (parseFloat(note.getAttribute('data-x')) || 0) + dx;
        const y = (parseFloat(note.getAttribute('data-y')) || 0) + dy;

        note.style.webkitTransform =
          note.style.transform =
            `translate(${x}px, ${y}px)`;

        note.setAttribute('data-x', x);
        note.setAttribute('data-y', y);
      },
    },
  };
</script>

<style rel="stylesheet/scss" lang="scss">
  .note {
    background: yellow;
    width: 200px;
    height: 200px;
    transform: translate(0px, 0px);
    position: absolute;

    .handle {
      height: 10px;
      background: red;
    }

    &.selected {
      border: 2px solid black;
    }
  }
</style>



